@use '../../styles/shared/backdrop';

:host {
    display: contents;
}

#dialog {
    position: fixed;
    inset: 0;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    max-height: calc(100dvh - 48px);
    overflow: hidden;
    border: 0;
    padding: 0;
    background: transparent;
    z-index: var(--token-z-index-modal);
    transform-origin: center;
    opacity: 0;
    scale: 0.95;
    box-shadow: var(--token-shadow-3);
    transition:
        translate 0.3s ease,
        scale 0.3s ease,
        opacity 0.3s ease,
        visibility 0s linear 0.3s;
    visibility: hidden;

    .backdrop {
        display: flex;
        max-height: calc(100dvh - 48px);
    }

    &[open] {
        translate: 0 0;
        scale: 1;
        opacity: 1;
        visibility: visible;
        transition:
            translate 0.3s ease,
            scale 0.3s ease,
            opacity 0.3s ease,
            visibility 0s linear;

        @starting-style {
            opacity: 0;
            scale: 1.15;
        }
    }

    &:not([open]) {
        scale: 0.95;
        opacity: 0;
        visibility: hidden;
    }
}
