import {
    Component,
    Prop,
    h,
    Element,
    Method,
    State,
    Event,
    Host,
    EventEmitter,
    Listen,
} from '@stencil/core';

/**
 * @category Overlays
 * @description A modal dialog component for displaying content that requires user interaction or attention. Features backdrop click handling, escape key support, and programmatic open/close control.
 *
 * @slot - Display content within the dialog
 */
@Component({
    tag: 'at-dialog',
    styleUrl: 'at-dialog.scss',
    shadow: false,
})
export class AtDialogComponent {
    @Element() el!: HTMLAtDialogElement;

    /**
     * Role of the dialog element. Can be either 'dialog' or 'alertdialog'
     */
    @Prop()
    role: 'dialog' | 'alertdialog' = 'dialog';

    /**
     * Whether to show a backdrop behind the panel, prevents any interaction with background UI.
     */
    @Prop()
    backdrop: boolean = true;

    /**
     * Will close the dialog if clicked
     */
    @Prop()
    close_backdrop: boolean = false;

    /**
     * Data-id of an external element to use as the trigger. When provided, clicking the trigger will toggle the dialog.
     */
    @Prop() trigger_id?: string;

    /**
     * Internal state to track if dialog is open
     */
    @State()
    isOpen: boolean = false;

    /**
     * Emits an event when the dialog is toggled, with `event.detail` being true if the dialog is now open
     */
    @Event()
    atuiDialogChange: EventEmitter;

    private dialog: HTMLDialogElement;
    private dialogWrapper: HTMLElement;
    private triggerEls: HTMLElement[] = [];
    private externalTriggerListeners: Array<{
        element: HTMLElement;
        event: string;
        handler: EventListener;
    }> = [];

    /**
     * Toggles the dialog modal between open and closed states
     * @returns Promise that resolves when the dialog state is toggled
     */
    @Method()
    async toggleDialog(): Promise<void> {
        if (this.isOpen) {
            await this.closeDialog();
        } else {
            await this.openDialog();
        }
    }

    /**
     * Opens the dialog modal
     * @returns Promise that resolves when the dialog is opened
     */
    @Method()
    async openDialog(): Promise<void> {
        const dialog = this.el.querySelector('dialog') as HTMLDialogElement;
        if (dialog && !this.isOpen) {
            if (this.backdrop === true) {
                dialog.showModal();
            } else {
                dialog.show();
            }

            this.isOpen = true;
            this.atuiDialogChange.emit(this.isOpen);
        }
    }

    /**
     * Closes the dialog modal
     * @returns Promise that resolves when the dialog is closed
     */
    @Method()
    async closeDialog(): Promise<void> {
        const dialog = this.el.querySelector('dialog') as HTMLDialogElement;
        if (dialog && this.isOpen) {
            dialog.close();
            this.isOpen = false;
            this.atuiDialogChange.emit(this.isOpen);
            dialog.removeAttribute('open');
        }
    }

    /**
     * Getter method for the open state of the dialog
     * @returns The current open state of the dialog
     */
    @Method()
    async getIsOpen(): Promise<boolean> {
        return this.isOpen;
    }

    private handleDialogClose = () => {
        if (this.isOpen) {
            this.closeDialog();
        }
    };

    private handleKeyDown = (event: KeyboardEvent) => {
        if (event.key === 'Escape' && this.isOpen) {
            event.preventDefault();
            this.closeDialog();
        }
    };

    @Listen('mousedown', { target: 'document' })
    offClickHandler(event: any) {
        if (!this.close_backdrop || !this.dialog?.open) return;
        if (!this.dialogWrapper?.contains(event.target)) {
            console.log('asdasd');
            this.handleDialogClose();
        }
    }

    async componentDidLoad() {
        this.dialog = this.el.querySelector('dialog') as HTMLDialogElement;

        if (this.trigger_id) {
            this.triggerEls = Array.from(
                document.querySelectorAll(`[data-id="${this.trigger_id}"]`),
            ) as HTMLElement[];
            if (this.triggerEls.length === 0) {
                console.warn(
                    `atui-dialog: No elements found with data-id="${this.trigger_id}"`,
                );
                return;
            }
            this.setupExternalTriggerListeners();
        }

        if (this.backdrop) {
            this.dialog.classList.add('backdrop');
        }
    }

    disconnectedCallback() {
        this.cleanupExternalTriggerListeners();
    }

    private cleanupExternalTriggerListeners() {
        this.externalTriggerListeners.forEach(({ element, event, handler }) => {
            element.removeEventListener(event, handler);
        });
        this.externalTriggerListeners = [];
    }

    private setupExternalTriggerListeners() {
        if (!this.triggerEls || this.triggerEls.length === 0) return;

        const clickHandler = async (event: Event) => {
            event.preventDefault();
            event.stopPropagation();
            await this.toggleDialog();
        };

        const keydownHandler = async (event: KeyboardEvent) => {
            switch (event.key) {
                case 'Enter':
                case ' ':
                    event.preventDefault();
                    await this.toggleDialog();
                    break;
            }
        };

        this.triggerEls.forEach((el) => {
            el.addEventListener('click', clickHandler);
            el.addEventListener('keydown', keydownHandler);
            this.externalTriggerListeners.push(
                { element: el, event: 'click', handler: clickHandler },
                { element: el, event: 'keydown', handler: keydownHandler },
            );
        });
    }

    render() {
        return (
            <Host>
                <dialog
                    part="dialog"
                    id="dialog"
                    class={`${this.backdrop ? 'backdrop' : ''}`}
                    data-name="dialog"
                    role={this.role}
                    aria-modal="true"
                    onClose={this.handleDialogClose}
                    onKeyDown={this.handleKeyDown}
                >
                    <div
                        part="backdrop"
                        id="backdrop"
                        class="backdrop-content"
                        ref={(el) => (this.dialogWrapper = el)}
                    >
                        <slot />
                    </div>
                </dialog>
            </Host>
        );
    }
}
